#choosing-box {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%,-50%);

    .header {
        padding: 30px 30px 19px;
        background-color: #f8f8f8;
    }

    .footer {
        background-color: #e1e4e7;
        padding: 30px 30px 13px;

        .fa-asterisk {
            color: #9e9e9e;
            font-size: 6px;
            transform: translateY(-50%);
        }

        a {
            color: #335075;
            text-decoration: none;
            font-size: 20px;
            line-height: 1.65em;
        }
    }

    .logo {
        width: 283px;
    }

    .btn {
        margin: 0px 15px;
    }
}


#bg {
    width: 200%;
    height: 200%;
    position: fixed;
    top: -50%;
    right: -50%;

    img {
        min-width: 50%;
        min-height: 50%;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
    }
}